* {
	font-family: Helvetica, Arial, sans-serif;
	background-color: rgb(218, 221, 235);
}

body {
	padding: 60px;
}

input {
	color: gray;
	font-size: 1.2rem;
	width: 100%;
}

.card-grid {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
}

.card {
	align-content: center;
	border: 1px solid grey;
	border-radius: 6px;
	margin: 10px;
	overflow: hidden;
}

.card p {
	padding: 5px;
	text-align: center;
}

.card img {
	max-height: 200px;
}
